// 主函数挂载

Vue.config.productionTip = false

// (此方法挂载无法实现接口上的复用)
// 设置axios默认请求路径
axios.defaults.baseURL = 'http://www.lockeart.top:3006'
// vue挂载axios，以后直接调用$http
Vue.prototype.$http = axios

new Vue({
  //使用router文件夹下index.js导出的路由实例对象
  //router:router 属性名和属性值一样可简写为router
  router,
  render: h => h(App)
}).$mount('#app')


router.beforeEach((to,from,next)=>{
  if (to.meta.title){document.title = to.meta.title;}
    // 路由跳转后，让页面回到顶部
    document.body.scrollTop = 0; 
    document.documentElement.scrollTop = 0; 
    window.pageYOffset = 0; 
    // next() 放行 next('/login') 强制跳转
    // 一定要调用next方法，否则钩子就不会被销毁!!
  next();
})


