          // text-decoration: underline !important;
          border-bottom: 1px solid #247feb;
        }

        a:hover {
          cursor: pointer;
          color: #2bc2fd;
          border-bottom: 1px solid #2bc2fd;
          // padding-left: 10px;
        }
      }

      li::after {
        // font-family: "Heebo", -apple-system, BlinkMacSystemFont, "Segoe UI",
        //   Roboto, "Helvetica Neue", Oxygen, Ubuntu, Cantarell, "Open Sans",
        //   Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
