        font-weight: normal;
        // font-variation-settings: "wght" 600;
        left: 0.5rem;
        opacity: 0.5;
        // box-sizing: border-box;
      }

      li:last-child::after {
        display: none;
      }

      .el-link {
        margin-bottom: 5px;
        border-bottom: 1px solid #409eff;
      }
    }
  }

  // 下载按钮
  .title_button_container {
    text-align: center;
    margin: 0 8%;
    display: flex;
    align-items: center;
    justify-content: center;

    .download-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fefefe;
      height: 50px;
      padding: 0;
      background: #009578;
      border: none;
      outline: none;
      border-radius: 5px;
      // overflow: hidden;
      font-family: "Quicksand", sans-serif;
      font-size: 16px;
      font-weight: 500;
      cursor: pointer;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      -webkit-transition-duration: 0.3s;
      transition-duration: 0.3s;
      -webkit-transition-property: box-shadow transform;
      transition-property: box-shadow transform;
    }

    .download-btn:hover,
    .download-btn:focus,
    .download-btn:active {
      box-shadow: 0 0 17px rgba(0, 0, 0, 0.3);
      -webkit-transform: scale(1.02);
      transform: scale(1.02);
    }

    .button_text,
    .button_svg {
      padding: 24px;
    }

    .button_svg {
      background: #ffffff35;
    }
  }
