.index_page_one {
  height: 530px;
  background: #fef6e4;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15% 4%;
  margin-top: 6%;

  .img_container_1 {
    height: 100%;

    // padding: 150px 5% 0px 5%;
    // img {
    //   width: 100%;
    //  height: auto;

    // }
  }

  .accordion {
    position: relative;
    .accordion_title {
      position: absolute;
      top: -100px;
      left: 12%;
      color: #001858;
      h1 {
        padding-bottom: 15px;
        font-weight: 700;
      }
    }

    .accordion_container {
      position: absolute;
      top: 55px;
      left: 12%;
      margin-left: 0;
      // margin-top: 105px;#abd1c6
      width: 86%;
      background-color: #2a9ead;

      .accordion_item {
        border: none;
        padding-bottom: 0;
        box-shadow: 0 0 0 1px #66666670;
        color: #eee;

        .change_title_color {
          background-color: #7eaf81;
        }

        .change_content_color {
          background-color: #004643;
          color: #abd1c6;
        }

        .change_title_color2 {
          background-color: #9d91bd;
        }

        .change_content_color2 {
          background-color: #232946;
          color: #b8c1ec;
        }
      }

      .accordion_header {
        // font-size: 18px;
        // padding: 10px;
        // padding-left: 15px;
        // background-color: #2a9ead;
        // p {
        text-decoration: none;
        display: block;
        padding: 10px 20px;
        background: #2a9ead;
        font-size: 16px;
        // text-transform: capitalize;
        font-weight: 300;
        color: #fff;
        display: block;
        border-bottom: 1px solid #363131;
        transition: all 0.3s ease;
        cursor: pointer;
        // }
      }

      .accordion_content {
        height: 0;
        overflow: hidden;
        background-color: #232946;
        margin: 0;
        padding-left: 20px;
        padding-right: 15px;
        transition: 0.5s;
      }
      ul li {
        padding-top: 10px;
        // padding-bottom: 5px;
      }
      .active {
        p {
          color: #fff;
          padding-left: 40px;
        }
        ul {
          height: 75px;
        }
      }
    }
  }
}

.my_toast {
  position: absolute;
  top: 60px;
  right: 30px;
  border-radius: 12px;
  background: #fff;
  padding: 10px 25px 10px 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  border-left: 6px solid #4070f4;
  overflow: hidden;
  transform: translateX(calc(100% + 30px));
  transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.35);
  width: 300px;

  &.active {
    transform: translateX(0%);
  }

  .progress {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 3px;
    width: 100%;
    background: #ddd;
  }
  .progress:before {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    height: 100%;
    width: 100%;
    background-color: #4070f4;
  }

  .progress.active:before {
    animation: progress 5s linear forwards;
  }

  @keyframes progress {
    100% {
      right: 100%;
    }
  }

  .my_toast_content {
    display: flex;
    align-items: center;
    .my_message {
      display: flex;
      flex-direction: column;
      margin: 0 15px;
    }
    .text {
      font-size: 16px;
      font-weight: 400;
      color: #666;
    }
    .text.text_1 {
      font-weight: 600;
      color: #333;
    }
  }
}

