// 1.导入 Vue 和 VueRouter 包 
//！！！必须安装兼容的vue-router版本,
// vue2对应的版本是vue-router 3.5.2 卸载现在安装的版本
import Vue from 'vue'
import VueRouter from 'vue-router'

// 守卫路由 - 用于动态修改标签
// import {createRouter, createWebHashHistory} from 'vue-router'


//导入需要的组件
import Index from '@/components/Index'
import Community from '@/components/Community'
import Login_user from '@/components/Login_user'
import Login_admin from '@/components/Login_admin'
import About from '@/components/About'
import Tab1 from '@/components/tabs/Tab1'
import Tab2 from '@/components/tabs/Tab2'
import Document from '@/components/Document'



// 2.将 VueRouter 安装为 Vue 的插件
Vue.use(VueRouter)

// 3.创建路由的实例对象
const router = new VueRouter({
    //数组，定义hash地址与组件的对应关系
    routes: [ //父路由规则
        { path: '/', meta:{title:'Lockeart Painter - 图片聚合、灵感收集必备工具'}, redirect: '/index'},
        { path: '/index', meta:{title:'Lockeart Painter - 图片聚合、灵感收集必备工具'}, component: Index},
        { path: '/community', meta:{title:'Lockeart | 社区'}, component: Community},
        { path: '/document', meta:{title:'Lockeart | 开发者文档'}, component: Document},
        { path: '/about',meta:{title:'Lockeart | 作者'}, component: About},
        { path: '/login', meta:{title:'Lockeart | 用户登录'}, component: Login_user},
        { path: '/login_super', meta:{title:'Lockeart | 管理员登录'}, component: Login_admin,
            children: [ //子路由规则（''中的组件为默认子路由）
                { path: '', meta:{title:'Lockeart | 作者'}, component: Tab1},
                { path: 'tab1', component: Tab1},
                { path: 'tab2', component: Tab2},
            ]
        }
    ]
})

// 4.向外共享路由的实例对象
export default router

