<script>
export default {
  name: "App",
  data() {
    return {
      // activeIndex: "/index",
      isActive: 1,
      flexNav: false,
      options: [
        { value: "选项1", label: "中文" },
        { value: "选项2", label: "English" },
        { value: "选项3", label: "Elvish" },
        { value: "选项4", label: "русск" },
        { value: "选项5", label: "日本語" },
      ],
    };
  },
  methods: {
    // 导航栏项目被选中
    changeIndex(index) {
      this.isActive = index;
      this.showFlexNavBar();
    },
    // 展开被折叠的导航栏
    showFlexNavBar() {
      const navBar = document.querySelector(".nav_bar");
      navBar.classList.toggle("active");
      const line1 = document.querySelector("#line1");
      const line2 = document.querySelector("#line2");
      const line3 = document.querySelector("#line3");

      if (this.flexNav) {
        line1.style.cssText = "transform: rotate(0deg);";
        line2.style.cssText = "transform: rotate(0deg);";
        line3.style.cssText = "display: block;";
        this.flexNav = !this.flexNav;
      } else {
        line1.style.cssText = "transform: rotate(45deg);";
        line2.style.cssText = "transform: rotate(-45deg);margin-top:-7px;";
        line3.style.cssText = "display: none;";
        this.flexNav = !this.flexNav;
      }
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
  },
};
</script>




